import { useSelector } from 'react-redux';
import { addPRojectAC, selectProjectAC } from '../../store/rootReducer';
import store from '../../store/store';
import { keyCreator } from '../../utils/keyCreator';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';

const Projects = () => {
  const projects = useSelector((state) => state.root.projects);
  const navigate = useNavigate();
  const handleClick = (e) => {
    store.dispatch(selectProjectAC(e.target.innerText));
    navigate('/tasks');
  };

  const [showModal, setShowModal] = useState(false);
  const [projectName, setProjectName] = useState('');
  return (
    <div>
      {Object.keys(projects).map((el, index) => (
        <div className="project" onClick={handleClick} key={keyCreator(index)}>
          {el}
        </div>
      ))}
      <div>
        <div>
          {showModal ? (
            <>
              <input
                value={projectName}
                onChange={(e) => {
                  setProjectName(e.target.value);
                }}
                autoFocus={true}
                style={{ border: '1px solid #000' }}
                type="text"
              />
              <button
                onClick={() => {
                  store.dispatch(addPRojectAC(projectName));

                  setProjectName('');
                  setShowModal((prev) => !prev);
                }}>
                add
              </button>
            </>
          ) : (
            <></>
          )}
        </div>
        <button
          onClick={() => {
            setShowModal((prev) => !prev);
          }}>
          {showModal ? 'hide' : 'add project'}
        </button>
      </div>
    </div>
  );
};

export default Projects;
